import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import useMediaQuery from "@material-ui/core/useMediaQuery";
import Typography from '@material-ui/core/Typography';
// import AlternateEmailIcon from '@material-ui/icons/AlternateEmail';
// import AccountBoxIcon from '@material-ui/icons/AccountBox';

import AllGPData from './ui/AllGPData';

// import book5 from '../assets/gp-success/book5.jpg';
// import mbook5 from '../assets/gp-success/mbook5.jpg';

const useStyles = makeStyles(theme => ({
  mainContainer: {
    marginTop: 0,
    height: "100%",
  },
  imgContainer: {
    backgroundColor: theme.palette.common.greyLight,
    backgroundImage: data => `url(${data.gpImgLaptop.default})`,
    backgroundPosition: "left",
    backgroundSize: "contain",
    backgroundRepeat: "no-repeat",
    backgroundAttachment: "fixed",
    [theme.breakpoints.down(750)]: {
      height: "400px",
      backgroundImage: data => `url(${data.gpImgMobile.default})`,
      backgroundSize: "cover",
      backgroundPosition: "bottom",
      backgroundAttachment: "initial",
    },
  },
  bookImg: {
    height: "100%",
    width: "auto",
    position: "fixed",

  },
  storyContainer: {
    padding: "3em 10em",
    backgroundColor: theme.palette.common.greyLight,
    [theme.breakpoints.down("md")]: {
      padding: "3em 3em",
    },
    [theme.breakpoints.down("xs")]: {
      padding: "3em 1.5em",
    }
  },
  a: {
    color: theme.palette.common.color1,
    textDecoration: "none",
  },
}));

export default function GPWinnerDetailsPage(props) {

  let propsID = 0;
  if (props.location.state !== undefined) {
    propsID = props.location.state.id;
  }

  const gpID = propsID;
  const gpArray = AllGPData();

  let data = gpArray.find((element) => {
      return element.id === gpID;
    });

  if (data === undefined) {
    data = {
      id: -1,
      name: "404 - No Data Found",
      desc: "",
      avatar: require('../assets/imagePlaceHolder.png'),
      gpImgLaptop: require('../assets/gp-success/book5.jpg'),
      gpImgMobile: require('../assets/gp-success/mbook5.jpg'),
      content: "",
      link: "/GP-Success-Stories/GP-Details",
    }
  }
  console.log("data");
  console.log(data);

  const classes = useStyles(data);
  const theme = useTheme();

  const matches = useMediaQuery(theme.breakpoints.down(750));

//On Line 100: <Button variant="outlined" color="primary"><a href = {data.pdf} target = "_blank" rel="noreferrer" className={classes.a}>Click here to Download as PDF</a></Button>

  return (
    <Grid container direction={matches ? "column" : "row"} className={classes.mainContainer}>
      <Grid xs={matches ? false : 4} item className={classes.imgContainer}> {/*------------Img Header Block----------------*/}</Grid>
      <Grid xs={matches ? false : 8} item className={classes.storyContainer}> {/*----------Select Year Block-----------*/}
        <Typography variant="h1"><span style={{color: theme.palette.common.color1}}>{data.name}</span></Typography>
        <br/> <br/>
        <Typography>
          {data.content}
        </Typography>
      </Grid>
    </Grid>
  );
}
