      content: <React.Fragment>21 ઓક્ટોબર 2020<br/><br/>

<Typography variant="h3">પ્રસ્તાવના</Typography> <br/><br/>

ખોખરા ઉમર ગ્રામ પંચાયત ગુજરાત રાજ્યના નર્મદા જિલ્લાના દેડિયાપાડા તાલુકામાં આવેલી છે. (2011 પ્રમાણે વસ્તી: કુલ 1,810, સ્ત્રીઓ 901, પુરુષ 909). ખોખરા ઉમર ગામ દેડિયાપાડા, જે નજીકનું શહેર અને તાલુકા મથક છે, ત્યાંથી લગભગ 3 કિ.મી. અને રાજપીપળા (જિલ્લા મથક)થી 48 કિ.મી. દૂર છે. 6 વર્ષથી નીચેના બાળકોની વસ્તીમાં કુલ 224 (સ્ત્રી 102, પુરુષ 122) સામેલ છે. ગામ મુખ્યત્વે આદિવાસી લોકો દ્વારા વસેલું છે અને અન્ય લોકોમાં ફક્ત 34 વ્યક્તિઓ (9 સ્ત્રી, 25 પુરુષો) સામેલ છે. <br/><br/>

<Typography variant="h3">પ્રમુખ સિદ્ધિઓ </Typography> <br/><br/>

<strong>શાસન:</strong>  શ્રી દિનેશભાઇ 2015થી સરપંચ છે. તેમને એવું કામ કરવામાં રસ છે કે જેથી લોકો હંમેશા તેમને યાદ કરે. દર વર્ષે ગ્રામ પંચાયત વિકાસ આયોજન (જી.પી.ડી.પી.) તૈયાર કરવામાં આવે છે. ગ્રામ સભામાં 60થી 70 લોકોની હાજરી હોય છે અને એક વર્ષમાં ઓછામાં ઓછી 2 ગ્રામસભાઓ યોજાય છે. "પેસા" સમિતિઓ સહિત ઘણી સમિતિઓ ગામમાં છે, પરંતુ સક્રિય નથી. <br/><br/>

<strong>બાળ સુપોષણ:</strong>  ગ્રામ પંચાયતમાં ચારથી પાંચ કુપોષિત બાળકો છે. ગામમાં ત્રણ આંગણવાડી કેન્દ્રો છે. અગાઉ આંગણવાડી કેન્દ્રોનું કામ માત્ર ભોજન આપવાનું હતું. સરપંચ અને વોર્ડના સભ્યોની સહાયક દેખરેખથી હવે આંગણવાડી કેન્દ્રો બાળપણનું શિક્ષણ અને રમત જેવી સેવાઓ પણ આપી રહ્યા છે. <br/><br/>

<strong>શિક્ષણ:</strong>  ગ્રામ પંચાયતમાં એક પ્રાથમિક શાળા છે, જેમાં 1થી 8 ધોરણો છે અને 9થી 10 ધોરણોવાળી બે આશ્રમ શાળાઓ છે. આ તમામ સંસ્થાઓમાં કુલ 13 શિક્ષકો અને 285 વિદ્યાર્થીઓ છે. દસમા ધોરણના બાળકો પરીક્ષામાં નિષ્ફળ થતા હતા અને ત્યારબાદ તેઓ ભણવાનું છોડી દેતા હતા, જે શાળાના શિક્ષણની નબળી ગુણવત્તા દર્શાવે છે. તેથી સરપંચે 9મા ધોરણના બાળકો માટે વિજ્ઞાન, ગણિત અને અંગ્રેજી વિષયોના  ટ્યુશન વર્ગો રજૂ કર્યા. પરિણામે તમામ બાળકો હવે 10માં ધોરણમાં પાસ થઈ રહ્યા છે અને 11માં ધોરણમાં પ્રવેશ લઈ રહ્યા છે. <br/><br/>

શ્રી દિનેશભાઇએ 2015માં બધા બાળકો માટે તેમના ગામમાં એક લાઇબ્રેરીની સ્થાપના પણ કરી. એ.કે.આર.એસ.પી. સંસ્થા દ્વારા કમ્પ્યુટર અને વીજળી ખર્ચ માટે નાણાંકીય ટેકો મળ્યો હતો. <br/><br/>

<Typography variant="h3">પ્રક્રિયા: ગ્રામ પંચાયતે કેવી રીતે સફળતા મેળવી? </Typography> <br/><br/>

શ્રી દિનેશભાઈ ઉત્સાહી છે અને તેમના ગામમાં શિક્ષણ સુધારવાની દીર્ઘદ્રષ્ટિ ધરાવે છે. જીપીડીપીની તૈયારી અને અમલીકરણ માટે એકેઆરએસપી પણ ગ્રામ પંચાયતને સમર્થન આપી રહી છે. ગ્રામ પંચાયતને નાણા પંચ તરફથી દર વર્ષે રૂ. 30 લાખની ગ્રાન્ટ મળે છે. મનરેગા ગ્રાન્ટ રૂ. 60 લાખ છે. આ ઉપરાંત રાજ્ય અને કેન્દ્ર સરકારની અન્ય યોજનાઓ અને ગ્રાન્ટ પણ ઉપલબ્ધ હોય છે. મકાન અને સ્વચ્છતા વેરામાંથી ગ્રામ પંચાયતની આવક વર્ષે રૂ. 1.5 લાખ છે. ગ્રામ પંચાયતના માનવ સંસાધનમાં તલાટી, પટાવાળા અને કમ્પ્યુટર કર્મચારીનો સમાવેશ થાય છે. <br/><br/>

બાળ લગ્ન એક પડકાર છે અને ગ્રામ પંચાયત તેને દૂર કરવા પ્રયત્નો કરી રહી છે. ભવિષ્યમાં ગ્રામ પંચાયતનું લક્ષ્ય શિક્ષણ ક્ષેત્રે વધુ કામ કરવાનું છે. <br/><br/>
</React.Fragment>,
      link: "/gu/GP-Success-Stories/GP-Details",
