      content: <React.Fragment>23 ડિસેમ્બર 2020<br/><br/>

<Typography variant="h3">પ્રસ્તાવના</Typography> <br/><br/>

ગોવાલી ગ્રામ પંચાયત: ૨૦૧૧ની ગણતરી મુજબ વસ્તી: કુલ 4,381, સ્ત્રીઓ 2,084, પુરુષો 2,297. ગોવાલી ગુજરાત રાજ્યના ભરૂચ જિલ્લાના ઝગડિયા તાલુકામાં આવેલું છે. આ ગામ ઝગડિયા, જે નજીકનું નગર અને તાલુકા મથક છે, તેનાથી લગભગ 10 કિ.મી. પર આવેલું છે અને ભરૂચ (જિલ્લા મથક)થી 14 કિ.મી પર. 6 વર્ષથી નીચેના બાળકોની વસ્તીમાં કુલ 444 (સ્ત્રી 206, પુરુષ 238) શામેલ છે. આ ગામમાં લગભગ એક તૃતીયાંશ આદિવાસી લોકોની વસ્તી છે અને અન્યમાં 225 વ્યક્તિઓ (103 સ્ત્રીઓ, 122 પુરુષો) અનુસૂચિત જાતિના અને બાકીના અન્ય સમુદાયોના લોકોનો સમાવેશ થાય છે. <br/><br/>


<Typography variant="h3">પ્રમુખ સિદ્ધિઓ </Typography> <br/><br/>

<strong>શાસન:</strong>  શ્રીમતી સુમનબેન વસાવા 2016થી સરપંચ છે. શ્રી પુરુષોત્તમભાઇ પટેલ શાળા વ્યવસ્થાપન સમિતિ (એસએમસી)ના  સભ્ય છે, કુ. જલ્પા વસાવા ગ્રામ વિકાસ ટ્રસ્ટના કમ્યુનિટિ ફેસિલિટેટર છે અને શ્રીમતી હંસાબેન "કોપ" સભ્ય છે. પ્રાથમિક શાળાના શિક્ષણમાં સુધારણા માટે (અગાઉ રહાડના કેસની વાતમાં વર્ણવ્યા મુજબ) "કોપ"ના સભ્યો સ્થાનિક શાસનમાં મોટો ફાળો આપે છે. <br/><br/>

<strong>શિક્ષણ:</strong>  1થી 8 ધોરણની એક પ્રાથમિક શાળા છે. રહાડ (અગાઉના ઉદાહરણ)ની જેમ, ગોવાલી સ્કૂલે પણ શાળામાં બધા પાત્ર બાળકોની 100% નોંધણી મેળવી છે, અને લગભગ તમામ બાળકોની 100% હાજરી શાળામાં રોજેરોજ હોય છે. <br/><br/>

શાળામાં બાળકોની નિયમિત હાજરી એ એક મોટો પડકાર હતો, કારણ કે માતાપિતા દૈનિક વેતન મજૂરી કરવા જાય છે, અને બાળકોની સંભાળ મોટા ભાઇ-બહેનો અથવા વૃદ્ધ લોકો પર છોડી દેવામાં આવે છે. આની અસર શાળાના શિક્ષણ પરિણામો પર પડતી હતી. <br/><br/>

ગોવાલીમાં પણ "કોપ"માં 11 સભ્યો છે, જેમાં 2 એસએમસી સભ્યો પણ સામેલ છે. "કોપ"ના સભ્યો ખાતરી કરે છે કે બાળકો શાળાના સમય દરમિયાન શાળામાં હોવા જોઈએ, અને ઘરોમાં અથવા શેરીઓમાં ફરતા ન હોવા જોઈએ. કોવિડ-19 ને કારણે ગ્રામ વિકાસ ટ્રસ્ટની ટીમે  એક સર્વેક્ષણ હાથ ધર્યું હતું, જેમાં ટીવી સેટ, સ્માર્ટ ફોન અને ઇન્ટરનેટ ન હોય તેવા ઘરો અલગ તારવીને તેઓને આવા  ઉપકરણો ધરાવતા ઘરો સાથે જોડીને નિયમિત વર્ગોમાં ભાગ લેવા માટેની વ્યવસ્થા ગોઠવી છે. આ કાર્યમાં "કોપ"ના સભ્યો અને શાળાના શિક્ષકો દ્વારા પણ મદદ કરવામાં આવી હતી. <br/><br/>

ગ્રામ પંચાયતે શાળાને મધ્યાહન ભોજનની વ્યવસ્થામાં છાંયા માટે શેડ અને શુદ્ધ પીવાના પાણી માટે આર.ઓ. મશીનથી મદદ કરી હતી. એક ખાસ પરિસ્થિતિમાં શાળાના પુનર્નિર્માણના મુદ્દા પર "કોપ" અને ગ્રામ પંચાયતના સભ્યોએ સાથે મળીને કામ કર્યું હતું. પુનર્નિર્માણ કાર્યનો સીધો કોન્ટ્રાક્ટ રાજ્ય સરકાર દ્વારા એક કોન્ટ્રાક્ટરને આપવામાં આવ્યો હતો. કોન્ટ્રાક્ટરે એમ વિચાર્યું કે આદિવાસી ગામ કંઈ કરી શકશે નહીં, તેથી તેણે કામને ગંભીરતાથી લીધું નહીં અને કામ અડધું છોડી દીધું. જો કે ગ્રામ પંચાયત અને "કોપ"ના સભ્યોએ  સાથે મળીને તેમના વિધાનસભાના સભ્યને રજૂઆત કરીને કોન્ટ્રાક્ટરને કામ આગળ વધારવાની ફરજ પાડી અને શાળાના પુનર્નિર્માણના કાર્યને પૂર્ણ કરવામાં આવ્યું. <br/><br/>

<Typography variant="h3">પ્રક્રિયા: ગ્રામ પંચાયતે કેવી રીતે સફળતા મેળવી? </Typography> <br/><br/>

ગોવાલી ગામમાં ગામની પ્રાથમિક શાળામાં ભણતા બાળકો માટે ગુણવત્તાયુક્ત શિક્ષણની ખાતરી કરવામાં ગ્રામ વિકાસ ટ્રસ્ટની કમ્યુનિટિ ફેસિલિએટર (સીએફ) અને "કોપ"ની વ્યૂહરચના મહત્વપૂર્ણ ભૂમિકા ભજવી રહી છે. (રહાડથી થોડું અલગ, ગોવલીની  "કોપ"માં સીએફ પણ સામેલ છે, કેમ કે ગામ વિશાળ અને દૂરનું છે). સીએફ એક હંગામી વ્યવસ્થા છે અને "કોપ" એ સમુદાય આધારિત લાંબા ગાળાની વ્યવસ્થા ,છે જે શાળામાં બધા પાત્ર બાળકોની નોંધણી અને હાજરી માટે કાર્યરત રહેશે. <br/><br/>
</React.Fragment>,
